import React from "react";
import { useEffect, useState } from "react";
import {
  MADContract,
  connectWallet,
  loadTotalMintCount,
  getCurrentWalletConnected,
  mintMDA,
  getSaleTime,
  getOwnerAddressHash,
  findWhitelist,
} from "./util/interact.js";

import {
	BrowserRouter as Router,
	Link
} from "react-router-dom";

import './App.css';
import {Button, Container, InputGroup, FormControl, Navbar,  Nav} from 'react-bootstrap'
import 'bootstrap/dist/css/bootstrap.min.css';
import gif_animation from './Assets/gif_animation.gif';
import bar_png from './Assets/bar.png';

let presale_start = "";
let publicsale_start = "";
getSaleTime().then(response => {
	presale_start = String(response.data.presale_start);
	publicsale_start = String(response.data.publicsale_start);
})
.catch(e => {
	console.log(e);
});


let is_list;
const MAD = () => {
	const [ownerAddress, setOwnerAddress] = useState("ss");
	const [walletAddress, setWallet] = useState("");
	const [totalMintCount, setTotalMintCount] = useState("0");
	const [mintCount, setMintCount] = useState(1);
	const [timeLeft, setTimeLeft] = useState({days:0,hours:0,minutes:0,seconds:0});
	
	const calculateTimeLeft = (is_whitelist) => {
		let time={};
		if(new Date(presale_start) - new Date() > 0)
		{
			var difference = new Date(presale_start) - new Date(); //2021-09-25T20:00:00.000+02:00
			if (difference > 0) {
				time = {
					days: Math.floor(difference / (1000 * 60 * 60 * 24)),
					hours: Math.floor((difference / (1000 * 60 * 60)) % 24),
					minutes: Math.floor((difference / 1000 / 60) % 60),
					seconds: Math.floor((difference / 1000) % 60)
				};
			}
			return time;
		}
		else if(new Date(presale_start) - new Date() < 0 && new Date(publicsale_start) - new Date() > 0 && is_whitelist === false)//whitelist
		{
			var difference = new Date(publicsale_start) - new Date(); //2021-09-25T20:00:00.000+02:00
			if (difference > 0) {
				time = {
					days: Math.floor(difference / (1000 * 60 * 60 * 24)),
					hours: Math.floor((difference / (1000 * 60 * 60)) % 24),
					minutes: Math.floor((difference / 1000 / 60) % 60),
					seconds: Math.floor((difference / 1000) % 60)
				};
			}
			return time;
		}
		else
		{
			return time;
		}
	}

	useEffect(() => {
		async function getInformation() {
			const count = await loadTotalMintCount();
			setTotalMintCount(count);
			setMintCount(1);
			addSmartContractListener();
			
			const { address,  } = await getCurrentWalletConnected();
			setWallet(address);
			addWalletListener();

			const owner_address = await getOwnerAddressHash();
			setOwnerAddress(owner_address);

			await findWhitelist(address.toLowerCase()).then(response => {
				if(response.data === "ok")
					is_list = true;
				else if(response.data === "poor")
					is_list = false;
			})
		}
		const startTimer = setInterval(() => {
			setTimeLeft(calculateTimeLeft(is_list));
		}, 1000);
		getInformation()
	}, [])

	const connectWalletPressed = async () => {
		const walletResponse = await connectWallet();
		setWallet(walletResponse.address);

		await findWhitelist(walletResponse.address.toLowerCase()).then(response => {
			if(response.data === "ok")
				is_list = true;
			else if(response.data === "poor")
				is_list = false;
		})
	};

	const onMintPressed = async() => {
		await mintMDA(walletAddress, mintCount);
	}

	function addSmartContractListener() {
		MADContract.events.JoinFace({}, (error, data) => {
			if (error) {
			} else {
			}
		});
	}

	function addWalletListener() {
		if (window.ethereum) {
		window.ethereum.on("accountsChanged", (accounts) => {
			if (accounts.length > 0) {
				setWallet(accounts[0]);
			} else {
				setWallet("");
			}
			async function setList()
			{
				await findWhitelist(String(accounts[0]).toLowerCase()).then(response => {
					if(response.data === "ok")
						is_list = true;
					else if(response.data === "poor")
						is_list = false;
				})
			}
			setList();
		});
		} else {
		}
	}

	const handleClickMintControl = (e, step) => {
		e.preventDefault();
		var tmp = mintCount + step;
		if(tmp < 1)
			tmp = 1;
		else if (tmp > 10)
			tmp = 10;
		setMintCount(tmp);
	}

	return (
	<>
	<Router>
		<Navbar collapseOnSelect expand='sm' style={{paddingTop:'0px',paddingBottom:'0px', position:'relative', backgroundColor:'rgba(85,71,173)'}}>
			<Container>
				<Navbar.Brand href="#"  style={{marginLeft:'0px', height:'70px'}} ></Navbar.Brand>
				<Navbar.Toggle aria-controls="basic-navbar-nav"/>
				<Navbar.Collapse id="basic-navbar-nav">
					<Nav className="mr-auto"  style={{fontSize:'25px'}}>
					<Link to="/"><Button style={{background: 'none', border: 'none',fontSize: '19pt',
							fontWeight:'bold',letterSpacing: '2pt'}}>Mutated Anti Doodles</Button>
					</Link>
					</Nav>
					<Nav className="me-auto">
					</Nav>
					<Nav className="ml-auto">
						<Button href="#Roadmap" variant="primary" style={{marginLeft:'10px',marginRight:'10px',background: 'none', border: 'none',fontSize: '15pt',}}>ROADMAP</Button>
						<Button href='https://twitter.com/madclubnft/' target="_blank" rel="noreferrer" variant="primary" className="navbar-link-block w-inline-block" style={{marginLeft:'10px',marginRight:'10px',background: 'none', border: 'none',fontSize: '15pt',}}>
							<img src="https://assets.website-files.com/61211faffaafcac4231a603c/61211faffaafcac62e1a608d_Twitter%20-%20Negative.svg" loading="lazy" alt=""/>
						</Button>
						<Button href='https://discord.gg/4MUZK5Te4x' target="_blank" rel="noreferrer" variant="primary" className="navbar-link-block w-inline-block" style={{marginLeft:'10px',marginRight:'10px',background: 'none', border: 'none',fontSize: '15pt',}}>
							<img src="https://assets.website-files.com/61211faffaafcac4231a603c/61211faffaafca5d3a1a608b_Discord%20-%20Negative.svg" loading="lazy" alt=""/>
						</Button>
						{
							walletAddress.toLowerCase() === ownerAddress.toLowerCase() ? (
								<Button href="/owner" rel="noreferrer" variant="primary" style={{ marginLeft:'10px',marginRight:'10px',background: 'none', fontSize: '15pt', fontWeight:'bold',borderWidth: '2px', borderRadius: '0px', borderColor: 'rgb(128 84 20)'}}>Owner</Button>
							) : 
							(<></>)
						}
					</Nav>
				</Navbar.Collapse>
			</Container>
		</Navbar>
		<img src={bar_png} className="topbar_png"/>
		<div className="page-wrapper">				
			<div className="main-section new-hero-section wf-section" id="Hero-Section">					
				<div className="main-container hero-container">
					<div className="hero-div-wrapper">
						<div className="hero-grid-left">
							<h1 className="timeline-heading">WELCOME TO<br/></h1>
							<h1 className="main-heading">Mutated Anti Doodles<br/></h1>
							<h1 className="yellow-section-paragraph ">A collection of 7,777 unique and cool version of Mutated Anti Doodles!<br/></h1>								
							<a href="https://discord.gg/4MUZK5Te4x" target="_blank" rel="noreferrer" className="main-button w-button">Join Our Discord
							</a>
							<div className="settings soldout" style={{display:"flex", justifyContent:'center', marginTop:'20px'}}>
								{
									walletAddress.length === 0 ? (
										<Button variant="primary" className="cta soldout mintbtn" style={{ marginLeft:'20px',backgroundColor: '#8C7DEA', fontSize: '15pt', fontWeight:'bold', borderRadius: '0px', width:'150px', height:'70px'}} onClick={connectWalletPressed}>Connect</Button>
									):
									( totalMintCount < 7778 
									? (
										<>
											<InputGroup className="mb-3" style={{marginTop:"5px", height:"60px"}}>
												<Button id="button-plus" style={{width:"45px"}} onClick={e => handleClickMintControl(e, -1)}>-</Button>
												<FormControl
												id="mint_input_cnt"
												aria-label="Example text with button addon"
												aria-describedby="basic-addon1"
												className="mint_input"
												readOnly
												value={mintCount}
												onChange={e => setMintCount(e.target.value)}
												/>
												<Button id="button-minus" style={{width:"45px"}} onClick={e => handleClickMintControl(e, 1)}>+</Button>
											</InputGroup>
										<Button variant="primary" disabled={timeLeft.seconds!==undefined} className="cta soldout mintbtn" style={{ marginLeft:'20px',backgroundColor: '#8C7DEA', fontSize: '15pt', fontWeight:'bold', borderRadius: '0px', width:'150px', height:'70px'}} onClick={onMintPressed}>Mint</Button>
										</>
									) 
									: (
										<Button variant="primary" href="https://opensea.io/collection/dafeiscollection" target="_blank" className="cta soldout mintbtn" style={{ marginLeft:'20px',backgroundColor: '#8C7DEA', fontSize: '15pt', fontWeight:'bold', borderRadius: '0px', width:'150px', height:'70px'}}>View On Opensea</Button>
									))
								}
							</div>
							{
								timeLeft.seconds !== undefined ? (
									<h3 className="timeline-heading" style={{textAlign:'center', fontSize:'30pt'}}>COMING SOON</h3>
								) : (<></>)
							}
						</div>
						<div className="hero-grid-right">
							<div className="lottie-animation">
								<img className="gif_img" src={gif_animation} alt=""/>
							</div>
						</div>
					</div>
				</div>
			</div>
										
			<div id="Roadmap" className="main-section timeline-section overflow-hidden wf-section">
				<div className="main-container">
					<h2 className="section-heading white center-align">ROADMAP</h2>
					<div className="timeline-grid-wrapper">
						<div className="timeline-left-wrapper">
							<div className="timeline-left-content-wrapper">
								<h1 className="timeline-heading">1/7 MAD Gang Welcome!</h1>
								<p className="timeline-paragraph">This is the beginning. Our Discord, Twitter, Instagram, and Website (soon) are launched! </p>
							</div>
							<div className="timeline-left-content-wrapper">
								<h1 className="timeline-heading">2/7 White List Presale and Public Launch</h1>
								<p className="timeline-paragraph">The Whitelisted members get a 24 hours head start before the public minters. Launch is TBA, a time will be set a week before we launch.</p>
							</div>
							<div className="timeline-left-content-wrapper">
								<h1 className="timeline-heading">3/7 Increase the Value of Your NFTs</h1>
								<p className="timeline-paragraph">You could be lucky and mint one of our legendary or rare NFTs and 2x, 5x or possibly 10x the value of your investment within few days just by holding it. Even if you do not get your hands on a legendary, the traits of your NFTs will prove its worth and we will implement strategies to increase the floor price over time.</p>
							</div>
							<div className="timeline-left-content-wrapper">
								<h1 className="timeline-heading">4/7 Rarity Tools Listing and Others</h1>
								<p className="timeline-paragraph">Apply to get Mutated Anti Doodles listed for rarity scores on multiple platforms as well as verification on OpenSea.</p>
							</div>
							<div className="timeline-left-content-wrapper">
								<h1 className="timeline-heading">5/7 Mutated Anti Doodles Over The World!</h1>
								<p className="timeline-paragraph">We will plan to do a huge marketing plan to show others and the whole world who the Mutated Anti Doodles are! We will show them who we are and get the right attention this project deserves.</p>
							</div>
							<div className="timeline-left-content-wrapper">
								<h1 className="timeline-heading">6/7 Merch Drop, A Custom MAD Merch Line!</h1>
								<p className="timeline-paragraph">Including shirts, hoodies, jackets and MORE TBD</p>
							</div>
							<div className="timeline-left-content-wrapper">
								<h1 className="timeline-heading">7/7 Private Meetup Events</h1>
								<p className="timeline-paragraph">There will be a first private party event hosted for all holders to celebrate the achievement of our goals and the success of Mutated Anti Doodles, we will let the community pick where the private event should be hosted. Much more to come... </p>
							</div>
							<div className="timeline-left-content-wrapper">
								<h1 className="timeline-heading">8/7 Utility and Future.</h1>
								<p className="timeline-paragraph">As our team has discussed recently, we realized we want to give back to this community driven NFT with a little more utility. For us, the best way to do this is through a token. We want to incorporate this token for early access for holders and have it run in the longterm. More to share in the future!</p>
							</div>
						</div>
						<div className="timeline-right-wrapper">
							<img src="https://assets.website-files.com/61211faffaafcac4231a603c/612a75f12765f9c8aa19a9bf_dotted%20lines.png" loading="lazy" width="101" alt="" className="image-59"/>
						</div>
					</div>
				</div>
			</div>

			<div className="main-section footer-section overflow-hidden wf-section" style={{backgroundColor:'rgba(85,71,173,0.6)'}}>
				<div className="main-container">
					<div className="footer-wrapper">
						<div className="footer-left-wrapper">
							<div className="left-content-wrapper">
								<p className="footer-paragraph">You can find us on 
								<a href="https://twitter.com/madclubnft/" target="_blank" rel="noreferrer" className="footer-link"><span className="footer-blue-highlight">Twitter</span></a> and 
								<a href="https://discord.gg/4MUZK5Te4x" target="_blank" rel="noreferrer" className="footer-link"><span className="text-span-7">Discord</span>.</a></p>
							</div>
						</div>
					</div>
				</div>
				<a href="#Hero-Section" className="scroll-up-link-block w-inline-block">
					<img src="https://assets.website-files.com/61211faffaafcac4231a603c/61213e7165485d053772825a_Arrow%201.png" loading="lazy" alt="" className="footer-arrow"/>
				</a>
			</div>
		</div>
	</Router>
	</>
	);
};

export default MAD;