const rinkebyKey = "https://eth-rinkeby.alchemyapi.io/v2/vNRrTL1PCwDlPNxCxSXb8BuhBAHYJ0Jc";
const { createAlchemyWeb3 } = require("@alch/alchemy-web3");
const web3 = createAlchemyWeb3(rinkebyKey);

const contractABI = require("../contract-abi.json");
const contractAddress = "0x47eD4B88e6925716ce6a6177F67cF9ba4A67Fe4D";
export const MADContract = new web3.eth.Contract( contractABI, contractAddress );

export const getContractBalance = async () => {
  var contract_balance;
  await web3.eth.getBalance(contractAddress, (err, balance) => { contract_balance = web3.utils.fromWei(balance) });
  return contract_balance;
}

export const getOwnerAddressHash = async () => {
  var owner_address = await MADContract.methods.owner().call();
  return owner_address.toLowerCase();
}

export const loadTotalMintCount = async () => {
  var message = await MADContract.methods.totalMint().call();
  return message;
};

export const connectWallet = async () => {
  if (window.ethereum) {
    try {
      const addressArray = await window.ethereum.request({
        method: "eth_requestAccounts",
      });
      const obj = {
        status: "👆🏽 Write a message in the text-field above.",
        address: addressArray[0],
      };
      return obj;
    } catch (err) {
      return {
        address: "",
        status: "😥 " + err.message,
      };
    }
  } else {
    return {
      address: "",
      status: (
        <span>
          <p>
            {" "}
            🦊{" "}
            <a  rel="noreferrer"  target="_blank" href={`https://metamask.io/download.html`}>
              You must install Metamask, a virtual Ethereum wallet, in your
              browser.
            </a>
          </p>
        </span>
      ),
    };
  }
};

